.basket{
    list-style-type:none;
    height: 500px;
    width: 250px;
    position: absolute;
    right:0rem;
    top:10rem;
    background-color: #50C594;/*#A0A4B0*/
    overflow: scroll;
  }
.basket img{
    height: 20px;
    width: 20px;
    cursor: pointer;
}

.delete{
  background-color: #EB5F5F;
  text-align: center;
  margin: 10px;
  cursor: pointer;
  transition: transform .2s;
}

.delete:hover{
  transform: scale(1.1)
}

.delete img{
  margin: 5px;
  
}

.row{
  transition: transform .2s;
  cursor: pointer;
  margin-top: 5px;
  background-color:#A0A4B0 ;
  display: flex;
  padding: 5px;
}

.row img{
  margin-right: 5px;
  margin-left: 10px;
}
.row img:hover{
  background-color: #EB5F5F;
}

.row:hover{
  transform: scale(1.1);
}